.profile {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 880px;
  margin: 0 auto;
  margin-top: 40px;
}

@media (max-width: 1000px) {
  .profile {
    flex-direction: column;
    justify-content: center;
    max-width: 282px;
  }
}

@media (max-width: 320px) {
  .profile {
    margin-top: 31px;
  }
}