.profile__add-button {
  width: 150px;
  height: 50px;
  background-size: cover;
  border: 0;
  border: 2px solid #FFFFFF;
  background-repeat: no-repeat;
  background-color: transparent;
	border-radius: 2px;
  padding: 0;
  transition: 0.5s;
  opacity: 1;
	cursor: pointer;
}

.profile__add-button:hover {
  opacity: 0.6;
}

@media (max-width: 1000px) {
  .profile__add-button {
    width: 282px;
  }
}